.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.root {
  height: 100%;
  width: 100%;
}

.flex-root {
  display: flex;
  flex-direction: row;
}

.overflow-control {
  overflow: scroll;
}

.route-container {
  height: 100%;
  max-height: 100vh;
  width: 90%;
  overflow: scroll;
}

@media only screen and (max-width: 600px),
screen and (orientation: portrait),
screen and (-webkit-min-device-pixel-ratio: 3) {
  .root {
    height: auto;
    /* height: 100vh; */
    max-height: auto;
    /* max-height: 100vh; */
    width: auto;
  }

  .flex-root {
    display: flex;
    flex-direction: column;
    height: auto;
    /* height: 100vh; */
    max-height: auto;
  }

  .route-container {
    height: auto;
    max-height: auto;
    /* width: 90%;
    overflow: scroll; */
    width: 100%;
    overflow: auto;
  }
}
